@font-face {
  font-family: "Inter";
  src: url("../fonts/Inter-ExtraBold.woff2") format("woff2"), url("../fonts/Inter-ExtraBold.woff") format("woff");
  font-weight: 800;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Inter";
  src: url("../fonts/Inter-Bold.woff2") format("woff2"), url("../fonts/Inter-Bold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Inter";
  src: url("../fonts/Inter-Medium.woff2") format("woff2"), url("../fonts/Inter-Medium.woff") format("woff");
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Inter";
  src: url("../fonts/Inter-Black.woff2") format("woff2"), url("../fonts/Inter-Black.woff") format("woff");
  font-weight: 900;
  font-style: normal;
  font-display: swap;
}
html {
  height: 100%;
  scroll-behavior: smooth;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

img {
  max-width: 100%;
  height: auto;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.container {
  width: 100%;
  max-width: 940px;
  padding: 0 24px;
  margin: 0 auto;
}

.page-body {
  font-family: "Inter", sans-serif;
  font-weight: 500;
  color: #686A6C;
  margin: 0;
  min-height: 100%;
  display: flex;
  flex-direction: column;
}

.header {
  padding: 24px 24px;
  max-height: 88px;
  position: relative;
  margin-bottom: 25px;
}

.header__container {
  display: flex;
  max-width: 940px;
  margin: 0 auto;
  min-height: 100%;
  border-bottom: 1px solid #E1E2E2;
  padding-bottom: 20px;
}
@media (min-width: 500px) {
  .header__container {
    display: block;
    padding-bottom: 0;
  }
}

.header__navigation {
  width: 100%;
  position: absolute;
  top: -100vh;
  left: 0;
  z-index: 1;
  text-align: center;
  transition: transform 0.5s ease-in;
}
@media (min-width: 500px) {
  .header__navigation {
    position: static;
    top: 0;
    left: 0;
  }
}

.header.open .header__navigation {
  transform: translateY(100vh);
}

.header.open .header__burger {
  position: relative;
}

.header.open .header__burger .burger__line,
.header.open .header__burger .burger__line::before {
  background-color: #ffffff;
}

.header.open .burger__line::after {
  display: none;
}

.header.open .header__burger .burger__line::before {
  transform: rotate(-90deg);
  top: 0;
}

.header.open .header__burger .burger__line {
  width: 24px;
  transform: rotate(45deg);
}

.header__contact {
  width: 100%;
  font-style: normal;
  text-align: left;
}
@media (min-width: 500px) {
  .header__contact {
    display: none;
  }
}

.header__contact_phone {
  text-decoration: none;
  text-transform: uppercase;
  color: #010101;
  font-size: 24px;
  line-height: 40px;
  font-weight: 900;
}

.burger {
  width: 44px;
  height: 44px;
  border: none;
  margin: 0;
  padding: 0;
  background-color: transparent;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 2;
}
.burger__line {
  display: block;
  width: 18px;
  height: 3px;
  background-color: #202025;
  border-radius: 4px;
  position: relative;
  transition: transform 0.5s ease-in;
}
.burger__line::before {
  content: "";
  position: absolute;
  width: 24px;
  height: 3px;
  border-radius: 4px;
  background-color: #202025;
  left: 0;
  top: -9px;
}
@media (min-width: 500px) {
  .burger {
    display: none;
  }
}

.navigation__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  background-color: #202025;
}
@media (min-width: 500px) {
  .navigation__list {
    display: flex;
    align-items: center;
    justify-content: space-between;
    background-color: transparent;
    padding: 0;
  }
}

.navigation__item:first-child {
  padding-top: 15px;
}

.navigation__item:not(:last-child) {
  border-bottom: 1px solid #686A6C;
}
@media (min-width: 500px) {
  .navigation__item:not(:last-child) {
    margin: 0;
    padding: 0;
    border: none;
  }
}

.navigation__link {
  display: block;
  padding: 22px 0;
  text-decoration: none;
  color: #ffffff;
  transition: color 0.3s ease-in;
}
@media (min-width: 500px) {
  .navigation__link {
    color: #686A6C;
  }
  .navigation__link.active {
    color: #010101;
  }
  .navigation__link:hover {
    color: #010101;
  }
}

.page-main {
  flex-grow: 1;
}

.hero {
  margin-bottom: 48px;
}

.hero .container {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 0;
}
@media (min-width: 768px) {
  .hero .container {
    display: grid;
    grid-template-columns: 1fr 1fr;
    column-gap: 20px;
    align-items: end;
    padding: 0 24px;
    align-items: center;
  }
  .hero .container picture {
    justify-self: end;
  }
}

.hero__info {
  text-align: center;
  padding: 0 24px;
}
@media (min-width: 768px) {
  .hero__info {
    padding: 0;
  }
}

.hero__title {
  margin: 0;
  margin-bottom: 32px;
  color: #202025;
  font-weight: 800;
  font-size: 48px;
  line-height: 60px;
  letter-spacing: -2px;
  text-align: left;
}
@media (min-width: 375xp) {
  .hero__title {
    font-size: 54px;
    line-height: 64px;
  }
}

.hero__subtitle {
  display: flex;
  align-items: center;
  color: #202025;
  font-size: 14px;
  line-height: 24px;
  font-weight: 800;
  text-transform: uppercase;
  letter-spacing: 5px;
  margin-bottom: 24px;
}

.hero__icon {
  margin-right: 20px;
}

.hero__text {
  margin: 0;
  margin-bottom: 24px;
  text-align: left;
  font-weight: 500;
  font-size: 16px;
  line-height: 24px;
  color: #686A6C;
}
.hero__text a {
  font-weight: bold;
  color: #686A6C;
}

.hero__avatar {
  display: block;
}
@media (min-width: 380px) {
  .hero__avatar {
    border-radius: 16px;
  }
}
@media (min-width: 768px) {
  .hero__avatar {
    object-fit: contain;
    object-position: top;
    transform: scale(0.9);
  }
}

.skills {
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin-bottom: 48px;
}

.skills__title {
  font-weight: 800;
  color: #202025;
  margin-top: 0;
  margin-bottom: 40px;
  font-size: 48px;
  line-height: 48px;
  text-align: left;
}
@media (min-width: 940px) {
  .skills__title {
    text-align: center;
  }
}

.skills__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  background-color: #F1F4F4;
  display: grid;
  grid-template-columns: 1fr 1fr;
  width: 100%;
  max-width: 892px;
}
@media (min-width: 768px) {
  .skills__list {
    grid-template-columns: repeat(3, 1fr);
  }
}

.skills__item {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 256px;
}
.skills__item:nth-child(1n) {
  border-right: 1px solid #E1E2E2;
}
.skills__item:nth-child(-n+4) {
  border-bottom: 1px solid #E1E2E2;
}
.skills__item:nth-child(2n) {
  border-right: none;
}
@media (min-width: 768px) {
  .skills__item:nth-child(1n) {
    border-right: none;
  }
  .skills__item:nth-child(-n+4) {
    border-bottom: none;
  }
  .skills__item:not(:nth-child(3n)) {
    border-right: 1px solid #E1E2E2;
  }
  .skills__item:nth-child(-n+3) {
    border-bottom: 1px solid #E1E2E2;
  }
}

.skills__img {
  margin-bottom: 16px;
}
@media (min-width: 768px) {
  .skills__img {
    transform: scale(1.1);
  }
}

.skills__name {
  font-weight: 700;
  font-size: 16px;
  line-height: 24px;
  color: #202025;
  display: inline-block;
  margin-bottom: 4px;
  color: #000000;
  font-weight: 700;
}

.skills__rating {
  display: flex;
  align-items: center;
}

.skills__rating_icon {
  margin-right: 4px;
}

.skills__rating span {
  font-weight: 700;
  font-size: 14px;
  line-height: 20px;
  color: #686A6C;
}

.portfolio {
  text-align: center;
  margin-bottom: 20px;
}
@media (min-width: 768px) {
  .portfolio {
    margin-bottom: 40px;
  }
}

.portfolio__title {
  font-weight: 800;
  color: #202025;
  margin-top: 0;
  margin-bottom: 32px;
  font-size: 48px;
  line-height: 48px;
}

.slider__button-wrapper {
  display: flex;
  justify-content: flex-start;
  margin-bottom: 40px;
}

.slider {
  border: 4px solid #E1E2E2;
  border-radius: 16px;
  width: 100%;
  max-width: 100%;
  overflow: hidden;
  margin: auto;
  max-height: 327px;
  position: relative;
  margin-bottom: 24px;
}
@media (min-width: 768px) {
  .slider {
    max-height: 500px;
  }
}

.slider__line {
  display: flex;
  position: relative;
  left: 0;
  transition: all ease 1s;
}

.slider__images {
  display: block;
  object-fit: contain;
  object-position: top;
}

.slider__button {
  border: 2px solid #E1E2E2;
  background-color: transparent;
  width: 60px;
  height: 60px;
  border-radius: 60%;
  outline: none;
}

.slider__button_prev {
  margin-right: 12px;
}

.slider__icon {
  width: 18px;
  height: 15px;
}

.slider__icon_prev {
  transform: rotate(-180deg);
}

.slider__button_next:hover,
.slider__button_prev:hover {
  background-color: #202025;
  border-color: #202025;
  fill: #ffffff;
}

.slider__alt_text {
  display: block;
  font-weight: 800;
  font-size: 20px;
  line-height: 36px;
  text-align: left;
  padding: 10px 10px;
  color: #202025;
  transition: all 0.5s;
  letter-spacing: -0.8px;
}

.main-footer {
  text-align: center;
  padding: 40px 0;
}

.footer__title {
  font-weight: 800;
  font-size: 48px;
  line-height: 48px;
  margin-top: 0;
  margin-bottom: 30px;
  color: #000000;
}

.footer__contact {
  font-style: normal;
  margin-bottom: 12px;
}

.footer__contact_phone {
  font-weight: 900;
  font-size: 24px;
  line-height: 40px;
  color: #010101;
  text-decoration: none;
}

.footer__link {
  display: block;
  background-color: #000000;
  max-width: 250px;
  padding: 13px;
  border-radius: 30px;
  color: #ffffff;
  margin: auto;
  text-decoration: none;
  margin-bottom: 30px;
}
.footer__link:hover {
  background-color: rgba(0, 0, 0, 0.7);
}

.footer__copyright {
  color: #A6AEAD;
  font-weight: 500;
  font-size: 14px;
  line-height: 20px;
  margin-bottom: 0;
  margin-top: 30px;
}

.social__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
  align-items: center;
  justify-content: center;
}

.social__item {
  margin-right: 20px;
}
.social__item:last-child {
  margin-right: 0;
}

.social__link {
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #000000;
  width: 44px;
  height: 44px;
  border-radius: 5px;
}
.social__link:hover {
  background-color: rgba(0, 0, 0, 0.7);
}