.hero {
    margin-bottom: 48px;
}
.hero .container {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 0;

    @media(min-width: $tablet) {
        display: grid;
        grid-template-columns: 1fr 1fr;
        column-gap: 20px;
        align-items: end;
        padding: 0 24px;
        align-items: center;

        & picture {
            justify-self: end;
        }
    }
}

.hero__info {
    text-align: center;
    padding: 0 24px;

    @media(min-width: $tablet) {
        padding: 0;
    }
}

.hero__title {
    margin: 0;
    margin-bottom: 32px;
    color: $black-alternate;
    font-weight: 800;
    font-size: 48px;
    line-height: 60px;
    letter-spacing: -2px;
    
    text-align: left;
    
    @media(min-width:$mobile) {
        font-size: 54px;
        line-height: 64px;
    }
}

.hero__subtitle {
    display: flex;
    align-items: center;
    color: $black-alternate;
    font-size: 14px;
    line-height: 24px;
    font-weight: 800;
    text-transform: uppercase;
    letter-spacing: 5px;
    margin-bottom: 24px;
}

.hero__icon {
    margin-right: 20px;
}

.hero__text {
    margin: 0;
    margin-bottom: 24px;
    text-align: left;
    font-weight: 500;
    font-size: 16px;
    line-height: 24px;
    color: $grey-text;

    & a {
        
        font-weight: bold;
        color: $grey-text
    }
}

.hero__avatar {
    display: block;
    @media(min-width: 380px) {
        border-radius: 16px;
    }
    @media(min-width: $tablet) {
        object-fit: contain;
        object-position: top;
        transform: scale(0.9);
    }
}