
.navigation__list {
    margin: 0;
    padding: 0;
    list-style-type: none;
    background-color: $black-alternate;

    @media(min-width: $tablet-xs) {
        display: flex;
        align-items: center;
        justify-content: space-between;
        background-color: transparent;
        padding: 0;
    }
}
.navigation__item:first-child {
    padding-top: 15px;
}
.navigation__item:not(:last-child) {
    border-bottom: 1px solid $grey-text;

    @media(min-width: $tablet-xs) {
        margin: 0;
        padding: 0;
        border: none;
    }
}

.navigation__link {
    display: block;
    padding: 22px 0;
    text-decoration: none;
    color: $white;
    transition: color .3s ease-in;
   
    @media(min-width: $tablet-xs) {
        color: $grey-text;
        &.active {
            color: $black-main;
        }

        &:hover {
            color: $black-main;
        }
    }
}