.main-footer {
    text-align: center;
    padding: 40px 0;
}

.footer__title {
    font-weight: 800;
    font-size: 48px;
    line-height: 48px;
    margin-top: 0;
    margin-bottom: 30px;
    color: $black;
}

.footer__contact {
    font-style: normal;
    margin-bottom: 12px;
}
.footer__contact_phone {
    font-weight: 900;
    font-size: 24px;
    line-height: 40px;
    color: $black-main;
    text-decoration: none;
}

.footer__link {
    display: block;
    background-color: $black;
    max-width: 250px;
    padding: 13px;
    border-radius: 30px;
    color: $white;
    margin: auto;
    text-decoration: none;

    margin-bottom: 30px;

    &:hover {
        background-color: rgba(0, 0, 0, .7);
    }
}

.footer__copyright {
    color: #A6AEAD;
    font-weight: 500;
    font-size: 14px;
    line-height: 20px;
    margin-bottom: 0;
    margin-top: 30px;
}