.burger {
    width: 44px;
    height: 44px;
    border: none;
    margin: 0;
    padding: 0;
    background-color: transparent;
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 2;

    &__line {
        display: block;
        width: 18px;
        height: 3px;
        background-color: $black-alternate;
        border-radius: 4px;

        position: relative;
        transition: transform 0.5s ease-in;

        &::before {
            content: "";
            position: absolute;
            width: 24px;
            height: 3px;
            border-radius: 4px;
            background-color: $black-alternate;
            left: 0;
            top: -9px;
        }
    }

    @media(min-width: $tablet-xs) {
        display: none;
    }

}