.social__list {
    margin: 0;
    padding: 0;
    list-style-type: none;

    display: flex;
    align-items: center;
    justify-content: center;
}


.social__item {
    margin-right: 20px;
    &:last-child {
        margin-right: 0;
    }
}

.social__link {
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: $black;
    width: 44px;
    height: 44px;

    border-radius: 5px;

    &:hover {
        background-color: rgba(0, 0, 0, .7);
    }
}
