.header {
    padding: 24px 24px;
    max-height: 88px;
    position: relative;
    margin-bottom: 25px;
}

.header__container {
    display: flex;
    max-width: 940px;
    margin: 0 auto;
    min-height: 100%;
    border-bottom: 1px solid $grey-border;
    padding-bottom: 20px;

    @media(min-width: $tablet-xs) {
        display: block;
        padding-bottom: 0;
    }
}

.header__navigation {
    width: 100%;
    position: absolute;
    top: -100vh;
    left: 0;
    z-index: 1;
    
    text-align: center;

    transition: transform 0.5s ease-in;

    @media(min-width: $tablet-xs) {
        position: static;
        top: 0;
        left: 0;
    }
}

.header.open .header__navigation{
    transform: translateY(100vh);
}

.header.open .header__burger {
    position: relative;
}

.header.open .header__burger .burger__line,
.header.open .header__burger .burger__line::before {
    background-color: $white;
}

.header.open .burger__line::after {
    display: none;
}


.header.open .header__burger .burger__line::before {
    transform: rotate(-90deg);
    top: 0;
}

.header.open .header__burger .burger__line {
    width: 24px;
    transform: rotate(45deg);
}

// Contacts
.header__contact {
    width: 100%;
    font-style: normal;
    text-align: left;
    

    @media(min-width: 500px) {
        display: none;
    }
}

.header__contact_phone {
    text-decoration: none;
    text-transform: uppercase;
    color: $black-main;
    font-size: 24px;
    line-height: 40px;
    font-weight: 900;
}


