.skills {
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin-bottom: 48px;
}
.skills__title {
    font-weight: 800;
    color: $black-alternate;
    margin-top: 0;
    margin-bottom: 40px;
    font-size: 48px;
    line-height: 48px;
    text-align: left;

    @media(min-width: $laptop) {
        text-align: center;
    }
}

.skills__list {
    margin: 0;
    padding: 0;
    list-style-type: none;
    background-color: $grey-bg;
    display: grid;
    grid-template-columns: 1fr 1fr;
    width: 100%;
    max-width: 892px;

    @media(min-width: $tablet) {
        grid-template-columns: repeat(3, 1fr);
    }
}

.skills__item {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 256px;

    &:nth-child(1n) {
        border-right: 1px solid $grey-border;
    }
    &:nth-child(-n+4) {
        border-bottom: 1px solid $grey-border;
    }
    &:nth-child(2n) {
        border-right: none;
    }

    @media(min-width: $tablet) {
        &:nth-child(1n) {
            border-right: none;
        }
        &:nth-child(-n+4) {
            border-bottom: none;
        }
        &:not(:nth-child(3n)) {
            border-right: 1px solid $grey-border;
        }
        &:nth-child(-n + 3){
            border-bottom: 1px solid $grey-border;
        }
    }
}

.skills__img {
    margin-bottom: 16px;
    @media(min-width: $tablet) {
        transform: scale(1.1);
    }
}

.skills__name {
    font-weight: 700;
    font-size: 16px;
    line-height: 24px;
    color: $black-alternate;
    display: inline-block;
    margin-bottom: 4px;
    color: $black;
    font-weight: 700;
}

.skills__rating {
    display: flex;
    align-items: center;    
}

.skills__rating_icon {
    margin-right: 4px;
}

.skills__rating span {
    font-weight: 700;
    font-size: 14px;
    line-height: 20px;
    color: $grey-text;
}