
.slider__button-wrapper {
    display: flex;
    justify-content: flex-start;
    margin-bottom: 40px;
}

.slider {
    border: 4px solid $grey-border;
    border-radius: 16px;
    width: 100%;
    max-width: 100%;
    overflow: hidden;
    margin: auto;
    max-height: 327px;
    position: relative;
    margin-bottom: 24px;

    @media(min-width: $tablet) {
        max-height: 500px;
    }
}

.slider__line {
    display: flex;
    position: relative;
    left: 0;
    transition: all ease 1s;
}

.slider__images {
    display: block;
    object-fit: contain;
    object-position: top;
}

.slider__button {
    border: 2px solid $grey-border;
    background-color: transparent;
    width: 60px;
    height: 60px;
    border-radius: 60%;
    outline: none;
}

.slider__button_prev {
    margin-right: 12px;
}

.slider__icon {
    width: 18px;
    height: 15px;
}

.slider__icon_prev {
    transform: rotate(-180deg);
}

.slider__button_next:hover,
.slider__button_prev:hover {
    background-color: $black-alternate;
    border-color: $black-alternate;
    fill: $white;
}

.slider__alt_text {
    display: block;
    font-weight: 800;
    font-size: 20px;
    line-height: 36px;
    text-align: left;
    padding: 10px 10px;
    color: $black-alternate;
    transition: all 0.5s;
    letter-spacing: -0.8px;
}